import React, {useState} from "react";
import './style.css'
// import 'bootstrap/dist/css/bootstrap.min.css';
import Grid from '@mui/material/Grid';
import Button from '@mui/material/Button';

const LoginPage = ({changeRoute})=>{

    const [email, setEmail] = useState('');
    const [hash, setHash] = useState('');

    const nameChange = (e)=>{
        setEmail(e.target.value)
    }

    const hashChange = (e)=>{
        setHash(e.target.value)
    }

    const submitLogin = (e)=>{
        e.preventDefault();
        console.log(email,hash)
        changeRoute('sucess')
    }

    return (
        <Grid container justifyContent={'center'}>
            <Grid item xl={4} xs={12} md = {5} lg={4} sm={5} >
                <Grid container justifyContent={'center'} sx={{marginBottom:'15px'}}>
                    <Grid item sx={{fontSize:'25px', fontWeight:'600'}}>
                        Welcome to expense tracker
                    </Grid>
                </Grid>
                <form onSubmit={submitLogin} method="get">
                <Grid container justifyContent={'center'} direction={'column'} className="bg" sx={{ padding:'8px', borderRadius:'15px', backgroundColor:'#f2f3f7', boxShadow:'1px 1px '}}>
                    <Grid item sx={{textAlign:'center',fontSize:'25px', fontWeight:'600', marginBottom:'5px'}}>
                        Log In
                    </Grid>
                    <Grid item sx={{marginBottom:'5px'}}>
                        <span style={{fontSize:'18px',fontWeight:'600'}}>Email</span>
                    <input type="email" className="form-control" placeholder="Enter your email" required="required" onChange={nameChange}/>
                    </Grid>
                    <Grid item sx={{marginBottom:'5px'}}>
                    <span style={{fontSize:'18px',fontWeight:'600'}}> Password</span>
                    <input type="password" className="form-control" placeholder="Enter your password" required="required" onChange={hashChange}/>
                    </Grid>
                    <Grid item>
                    <input type='submit' name='Log In' value='Sign In'/>
                    </Grid>
                    <Grid item sx={{marginTop:'15px'}}>
                        New user?               
                    <Button size='small' onClick={()=>changeRoute('register')}>Create an Account</Button>
                    </Grid>
                </Grid>
                </form>
            </Grid>
        </Grid>
    )
}

export default LoginPage;