import React, {useState} from "react";
import 'bootstrap/dist/css/bootstrap.min.css';
import './style.css'
import { Grid } from "@mui/material";
import { Button } from "@mui/material";

const RegisterPage = ({changeRoute})=>{
    
    const [email, setEmail] = useState('');
    const [hash, setHash] = useState('');
    const [cHash, setCHash] = useState('');
    const [firstName, setFirstName] =  useState('');
    const [lastName, setLastName] =  useState('');

    const submitRegister = (e)=>{
        console.log(e)
        changeRoute('signin')
    }

return (
    <Grid container justifyContent={'center'}>
            <Grid item xl={4} xs={12} md = {4} lg={4} sm={5} >
                <Grid container justifyContent={'center'} sx={{marginBottom:'15px'}}>
                    <Grid item sx={{fontSize:'25px', fontWeight:'600'}}>
                        Welcome to expense tracker
                    </Grid>
                </Grid>
                <form onSubmit={submitRegister}>
                <Grid container justifyContent={'center'} direction={'column'} className="bg" sx={{ padding:'8px', borderRadius:'15px', backgroundColor:'#f2f3f7', boxShadow:'1px 1px '}}>
                    <Grid item sx={{textAlign:'center',fontSize:'25px', fontWeight:'600', marginBottom:'5px'}}>
                        Register
                    </Grid>
                    <Grid item sx={{marginBottom:'5px'}}>
                        <Grid container justifyContent={'space-between'} spacing={.5}>
                            <Grid item xl={5.5} xs={12} sm={5.5}>
                            <input type="text" className="form-control" placeholder="First Name" required="required"/>
                            </Grid>
                            <Grid item xl={5.5} xs={12} sm={5.5}>
                            <input type="text" className="form-control" placeholder="Last Name" required="required"/>
                            </Grid>
                        </Grid>
                    </Grid>
                    <Grid item sx={{marginBottom:'5px'}}>
                        <Grid container justifyContent={'center'}>
                            <Grid item xl={12}xs={12}>
                            <input type="email" className="form-control" placeholder="Email" required="required"/>
                            </Grid>
                        </Grid>
                    </Grid>
                    <Grid item sx={{marginBottom:'5px'}}>
                        <Grid container justifyContent={'center'}>
                            <Grid item xl={12}xs={12}>
                            <input type="password" className="form-control" placeholder="Password" required="required"/>
                            </Grid>
                        </Grid>
                    </Grid>
                    <Grid item sx={{marginBottom:'5px'}}>
                        <Grid container justifyContent={'center'}>
                            <Grid item xl={12}xs={12}>
                            <input type="Password" className="form-control" placeholder="Confirm Password" required="required"/>
                            </Grid>
                        </Grid>
                    </Grid>
                    <Grid item>
                    <input type='submit' name='sign up' value='Register Now'/>
                    </Grid>
                    <Grid item sx={{marginTop:'15px'}}>
                        <Grid container justifyContent={'center'}>
                        Already have an account?               
                            <Button size='small' onClick={()=>changeRoute('signin')}>Sign in</Button>
                        </Grid>
                    </Grid>
                </Grid>
                </form>
            </Grid>
        </Grid>
)
}

export default RegisterPage;