import React, {useState} from "react";
import AddExpenseBar from "../AddExpenseBar/AddExpenseBar";
import './style.css'
// import ColumnChart from "../ColumnChart/ColumnChart";
import DailyExpense from "../DailyExpense/DailyExpense"
import Grid from '@mui/material/Grid';
import { toast } from 'react-toastify';
import MonthlyExpense from '../MonthlyExpense/MonthlyExpense'
import Nav from '../Nav/Nav'
import Tabs from '@mui/material/Tabs';
import Tab from '@mui/material/Tab';
import Box from '@mui/material/Box';

const Section = ({changeRoute})=>{

    const [itemsOfSelectedDate, setItemsOfSelectedDate] = useState([])
    const [duplicateItemObj, setDuplicateItemObj] = useState({})
    const [tab, setTab] = useState('daily')
    const [value, setValue] = React.useState(0);

    const notifyError = (text) => toast.error(text);
    const notifySucess = (text)=> toast.success(text);

    const confirmedItems = (obj)=>{
        if (duplicateItemObj[obj.name] === undefined){
            setItemsOfSelectedDate([...itemsOfSelectedDate, obj])
            notifySucess('Success')
        }else{
            notifyError('Item already added')
        }
        let obj1 = {}
        obj1[obj.name] = 1
        setDuplicateItemObj({...duplicateItemObj, ...obj1})
    }

    const totalDaysAmount = itemsOfSelectedDate.map(i=>i.amount)

    const expenseTabChange = (tab) =>{
        setTab(tab)
    }

    const handleChange = (event, newValue) => {
        setValue(newValue);
      };

    function a11yProps(index) {
        return {
          id: `simple-tab-${index}`,
          'aria-controls': `simple-tabpanel-${index}`,
        };
      }
      function TabPanel(props) {
        const { children, value, index, ...other } = props;
      
        return (
          <div
            role="tabpanel"
            hidden={value !== index}
            id={`simple-tabpanel-${index}`}
            aria-labelledby={`simple-tab-${index}`}
            {...other}
          >
            {value === index && (
              <Box sx={{ p: 3 }}>
                <typography>{children}</typography>
              </Box>
            )}
          </div>
        );
      }

    return (
        <Grid sx = {{marginBottom:'10px'}}>
            <Nav changeRoute={changeRoute} expenseTabChange={expenseTabChange} tab={tab}/>
            <Grid container justifyContent='center' sx={{marginTop:'15px'}}>
                <Grid item xl={5} md={8} xs={12} className='height100' sx={{padding:'3px', border:'1px black solid', borderRadius:'10px', boxShadow:'1px 1px', backgroundColor:'white'}}>
                <Tabs allowScrollButtonsMobile variant="scrollable" size='small' value={value} onChange={handleChange} aria-label="basic tabs example">
                    <Tab label="Daily Expense" {...a11yProps(0)} />
                    <Tab label="Montly Expense" {...a11yProps(1)} />
                    <Tab label="Reports" {...a11yProps(2)} />
                </Tabs>
                <TabPanel value={value} index={0}>
                    <AddExpenseBar confirmedItems={confirmedItems}/>
                    <DailyExpense itemsOfSelectedDate={itemsOfSelectedDate}/>
                    </TabPanel>
                    <TabPanel value={value} index={1}>
                        <MonthlyExpense msgPopUp = {{notifyError:notifyError, notifySucess:notifySucess}} dayExpenseList = {totalDaysAmount}/>
                    </TabPanel>
                    <TabPanel value={value} index={2}>
                    Reports
                </TabPanel>
                </Grid>
            </Grid>
        </Grid>
    )
}


export default Section;